import React from 'react';


class App extends React.Component {
  constructor(){
    super();
    this.state={};
  }
  
  


  handleChange=(e)=>{
    let vala=e.target.value
    fetch("http://freakh2o.pythonanywhere.com/get?hook="+e.target.value).then(response=>response.json()).then(data=>this.setState({hook:vala,user1:data["user1"],user2:data["user2"]}))
    
  }

  handleUp1=(e)=>{
    fetch("http://freakh2o.pythonanywhere.com/set?hook="+this.state.hook+"&u1val="+e.target.value+"&u2val="+this.state.user2)
    this.setState({user1:e.target.value})
    fetch("http://freakh2o.pythonanywhere.com/get?hook="+this.state.hook).then(response=>response.json()).then(data=>this.setState({user1:data["user1"],user2:data["user2"]}))

  }
  handleUp2=(e)=>{
    fetch("http://freakh2o.pythonanywhere.com/set?hook="+this.state.hook+"&u2val="+e.target.value+"&u1val="+this.state.user1)
    this.setState({user2:e.target.value})
    fetch("http://freakh2o.pythonanywhere.com/get?hook="+this.state.hook).then(response=>response.json()).then(data=>this.setState({user1:data["user1"],user2:data["user2"]}))

  }

  update=()=>{
    fetch("http://freakh2o.pythonanywhere.com/get?hook="+this.state.hook).then(response=>response.json()).then(data=>this.setState({user1:data["user1"],user2:data["user2"]}))
  }


  render(){
    setTimeout(this.update, 1000);
    return (
      <div>
        ENTER YOUR HOOK NUMBER
        <input onChange={this.handleChange}></input>
        <br></br>
        User1
        <textarea onChange={this.handleUp1}></textarea>
        <br></br>
        User2
        <textarea onChange={this.handleUp2}></textarea>
        <div>
          {this.state.user1}
        </div>
        <div>
          {this.state.user2}
        </div>   
      </div>
    );
  }
}

export default App;
